
/* add*/

export const sameItemCheck = (datas, name, process)=>{
  let sameItemCount = 0;
  datas.map((data)=>{
    if((data.name === name) && (data.process === process)){
        sameItemCount ++ ;
    }
  });
  if(sameItemCount >0){
      return true;
  }
  else{
      return false;
  }
}

